@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../../fonts/Montserrat-Regular.woff2") format("woff2"), url("../../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/Montserrat-Medium.woff2") format("woff2"), url("../../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/Montserrat-Bold.woff2") format("woff2"), url("../../fonts/Montserrat-Bold.woff") format("woff");
}
html {
  box-sizing: border-box;
  height: 100%;
  font-weight: 400;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #333333;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  border-style: none;
}

body {
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
}
@media (min-width: 768px) {
  .container {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding-right: 40px;
    padding-left: 40px;
  }
}

.page-header {
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  width: 100%;
  background-color: #ffffff;
  text-transform: uppercase;
  position: relative;
}

.page-header--nav-open .site-nav {
  display: flex;
  z-index: 1;
}
.page-header--nav-open .page-header__user-nav-toggle {
  background-image: url("../../img/icon-menu-cross.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 12px;
  height: 12px;
}
.page-header--nav-open .page-header__user-nav-toggle:active {
  background-color: #ffffff;
}

.page-header__container {
  display: flex;
  align-items: center;
}

.page-header__link {
  text-decoration: none;
  display: flex;
  margin-right: 27.53%;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .page-header__link {
    margin-right: 9.2%;
    padding-left: 0;
  }
}
@media (min-width: 1440px) {
