    justify-content: space-between;
  }
}
.page-header__user-nav a {
  font-size: 0;
  padding: 22px 22px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-header__user-nav a:not(:hover) {
  transition: 0.3s;
}
.page-header__user-nav a svg {
  fill: #333333;
}
@media (min-width: 1440px) {
