    font-size: 12px;
    line-height: 16px;
    padding-right: 21px;
    padding-left: 21px;
  }
  .page-header__user-nav a:active {
    background-color: #f0ebff;
    color: #7859cf;
  }
  .page-header__user-nav a:active svg {
    fill: #7859cf;
  }
  .page-header__user-nav a:hover {
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    color: #7859cf;
  }
  .page-header__user-nav a:hover svg {
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    fill: #7859cf;
  }
  .page-header__user-nav a:focus {
    background-color: #ffffff;
    color: #7859cf;
  }
  .page-header__user-nav a:focus svg {
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    fill: #7859cf;
  }
}

.page-header__user-nav-toggle {
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  background-image: url("../../img/icon-menu-burger.svg");
